@import url(https://fonts.googleapis.com/css2?family=Montserrat&display=swap);
body {
  margin: 0;
  padding: 0;
  font-family: 'Montserrat', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 60vmin;
  margin-bottom: 3em;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.Layout-body {
    background: linear-gradient(0deg,  #F6F5F8 70%, #E3E3E3 80%);
    min-height: 100vh;
    padding: 0 0.2em 1.7em 1.7em;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-size: calc(5px + 2vmin);
    color: #36454F;
}

.Layout-header {
    top: 0;
    background-color: #E3E3E3;
    width: 100%; /* Full width */
}

.Layout-footer {
    display: flex;
    background-color: #F6F5F8;
    justify-content: flex-end;
    width: 100%; /* Full width */
}

.Layout-link {
    color: #61dafb;
}

.Nav-right {
    margin: 0;
    padding: 0;
    display: flex;
    flex-direction: row;
    align-items: center;
    overflow: hidden;
}

.Nav-right a {
    padding: 1em;
    color: darkslategrey;
    text-decoration: none;
    font-weight: bolder;
}

.Nav-right a:hover, .Drop-down:hover #Drop-down-button{
    background-color: white;
}

.Standout-text {
    /*color: #FFE667;*/
    color: #C60021;
}

.Drop-down {
    float: left;
    overflow: hidden;
}

.Drop-down #Drop-down-button {
    font-size: inherit;
    display: inherit;
    border: none;
    outline: none;
    background-color: inherit;
    font-family: inherit;
    margin: 0;
}

.Drop-down-resume {
    display: none;
    background-color: #F6F5F8;
    box-shadow: 0px 8px 16px 0px rgba(0,0,0,0.2);
    z-index: 1;
}

.Drop-down-resume a {
    float: none;
    color: darkslategrey;
    padding: 12px 16px;
    text-decoration: none;
    display: inline-flex;
    text-align: left;
}

.Drop-down:hover .Drop-down-resume {
    display: flex;
    position: absolute;
    flex-direction: column;
}



.Resume-bullets {
    display: inherit;
    align-items: center;
    justify-content: center;
    padding-left: 30px ;
    width: 60%;
    margin: auto;
    text-indent: -30px ;
    text-align: left;
}

.Resume-bullets li {
    list-style-position: inside;
}

.Section-title {
    display: flex;
    margin: 0;
    padding: 0;
    text-align: center;
    align-items: center;
    justify-content: center;
}

.Resume-img {
    display: flex;
    align-items: center;
    justify-content: center;
    align-content: center;
}

.Resume-img img {
    width: 400px;
}

.School-information {
    display: inherit;
    margin: auto;
    text-align: center;
    width: 60%;
    font-size: calc(6px + 2vmin);
}

.Resume-title {
    margin: auto;
    width: 60%;
    display: flex;
    font-size: calc(6px + 2vmin);
}

.Coding-skills li{
    display: inline;
    padding-right: 1em;
}

ul {
    list-style: none;
}

.Resume-body {
    width: auto;
    margin: auto;
    display: flex;
    flex-direction: column;
}
